<template>
    <nav>
        <div class="nav-wrapper blue darken-2">
            <router-link to="/" class="brand-logo center">Employee Manager</router-link>
            <a href="#" data-target="mobile-demo" class="sidenav-trigger"><i class="material-icons">menu</i></a>
            <ul class="right hide-on-small-and-down">
                <li v-if="isLoggedIn"><span class="email black-text">{{ currentUser }}</span></li>
                <li><router-link v-if="isLoggedIn" to="/">Dashboard</router-link></li>
                <li><router-link v-if="!isLoggedIn" to="/login">Login</router-link></li>
                <li><router-link v-if="!isLoggedIn" to="/signup">Signup</router-link></li>
                <li><button v-if="isLoggedIn" @click="logout" class="btn grey">logout</button></li>
            </ul>
            <ul class="sidenav" id="mobile-demo">
                <li><a href="sass.html">Sass</a></li>
                <li><a href="badges.html">Components</a></li>
                <li><a href="collapsible.html">Javascript</a></li>
                <li><a href="mobile.html">Mobile</a></li>
            </ul>
        </div>
    </nav>
</template>

<script>
import firebase from 'firebase'
export default {
    name: 'navbar',
    data() {
        return {
            isLoggedIn: false,
            currentUser: false
        }
    },
    created() {
        if (firebase.auth().currentUser) {
            this.isLoggedIn = true
            this.currentUser = firebase.auth().currentUser.email
        }
    },
    methods: {
        logout() {
            firebase
            .auth()
            .signOut()
            .then(() => this.$router.push('/login'))
        }
    }    
}
</script>

<style scoped>

.email {
    padding-right: 10px;
}

</style>


