<template>
        <div class="container">
        <div class="col card hoverable blue-grey lighten-5">
            <form>
                <div class="card-content">
                    <h4 class="center-align">Sign Up</h4>
                        <div class="row">
                            <div class="input-field col s12">
                                <label for="email">Email address</label>
                                <input type="email" class="validate" name="email" id="email" v-model="email" />
                            </div>
                            <div class="input-field col s12">
                                <label for="password">Password </label>
                                <input type="password" class="validate" name="password" id="password" v-model="password"/>
                            </div>
                        </div>
                </div>
                <div class="card-action right-align">
                    <input type="reset" id="reset" class="btn-flat grey-text waves-effect">
                    <input @click="signup" type="submit" class="btn yellow black-text waves-effect waves-light" value="Sign Up">
                </div>
            </form>
        </div>
        </div>
</template>

<script>
import firebase from 'firebase'

export default {
    name: 'sign-up',
    data() {
        return {
            email: '',
            password: ''
        }
    },
    methods: {
        signup(e) {
            firebase.auth().createUserWithEmailAndPassword(this.email, this.password)
            .then(user => {
                alert('Account created for ' + user.user.email + '.');
                this.$router.push('/')
            }, error => alert(error.message))
            e.preventDefault();
        }
    }
}
</script>

<style scoped>

.card {
    margin-top: 30px;
}

</style>


