<template>
        <div class="container">
        <div class="col card hoverable blue-grey lighten-5">
            <form>
                <div class="card-content">
                    <h4 class="center-align">Log in</h4>
                        <div class="row">
                            <div class="input-field col s12">
                                <label for="email">Email address</label>
                                <input v-model="email" type="email" class="validate" name="email" id="email" />
                            </div>
                            <div class="input-field col s12">
                                <label for="password">Password </label>
                                <input v-model="password" type="password" class="validate" name="password" id="password" />
                            </div>
                        </div>
                </div>
                <div class="card-action right-align">
                    <input type="reset" id="reset" class="btn-flat grey-text waves-effect">
                    <input @click="login" type="submit" class="btn yellow black-text waves-effect waves-light" value="Login">
                </div>
            </form>
        </div>
        </div>
</template>

<script>
import firebase from 'firebase'
export default {
    name: 'login',
    data() {
        return {
            email: '',
            password: ''
        }
    },
    methods: {
        login(e) {
            firebase
            .auth()
            .signInWithEmailAndPassword(this.email, this.password)
            .then(user => {
                alert('You are logged in as ' + user.user.email + '.');
                this.$router.push('/')
            }, error => alert(error.message))
            e.preventDefault();
        }
    }
}
</script>

<style scoped>

.card {
    margin-top: 30px;
}

</style>


